<template>
  <div class="setting-wrapper">
      

      <yd-navbar title="My info">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon>back</yd-navbar-back-icon>
            </router-link>
        </yd-navbar>

    <yd-cell-group title="nickname">
          <yd-cell-item>
            <span slot="left"> </span>
            <yd-input slot="right" type="text" required max="20" placeholder="Your nickname" ></yd-input>
          </yd-cell-item>
      </yd-cell-group>
      
      <yd-cell-group title="skills">

        <skills></skills>
     
    </yd-cell-group>


    <yd-cell-group title="what you good at?">
          <yd-cell-item>
                <yd-input slot="right" type="text" required max="20" placeholder="" ></yd-input>
          </yd-cell-item>
      </yd-cell-group>

        <yd-cell-group class="">
            <yd-cell-item>
                <span slot="left">Average Ranking</span>
                <span slot="right"><el-rate
                v-model="4.6"
                disabled
                show-score
                text-color="#ff9900"
                score-template="{value}">
                </el-rate></span>
            </yd-cell-item>
           
     
        </yd-cell-group>

         
        <yd-cell-group class="demo-small-pitch">
            <yd-cell-item type="link" href="/account" arrow>
                <span slot="left">Change the password</span>
            </yd-cell-item>
            
        </yd-cell-group>

         <yd-cell-group class="demo-small-pitch">
            <yd-cell-item type="label">
                <span slot="left">I dont need internship any more</span>

                <yd-switch slot="right" v-model="invitation"> </yd-switch>

            </yd-cell-item>
            <p slot="bottom" style="text-align:left;padding:10px;color:#555;">You are no longer recive internship invitations</p>
            
        </yd-cell-group>

        

       


        
    <yd-button-group>
        <yd-button size="large" type="hollow" @click.native="Loginout">Quit</yd-button>
    </yd-button-group>

       
  </div>
</template>

<script>
import skills from '@/page/skills/skills'
import cookie from '../../static/js/cookie'
import {Cell, Switch as Vswitch , Accordion} from 'vue-ydui'
export default {
  data () {
    return {
        gender:[],
        invitation: false,
    }
  },
  components: {
      Cell,
      Vswitch,
      Accordion,
      skills
  },
  methods: {
      Loginout () {
          this.$router.push({path:'/login'})
      }
  }
}
</script>

<style lang="stylus" scoped>

</style>




